<template><undefined>
<h1>Switch 开关</h1>
<p>开关选择器。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>需要表示开关状态/两种状态之间的切换时；</li>
<li>和 <code v-pre="">checkbox</code>的区别是，切换 <code v-pre="">switch</code> 会直接触发状态改变，而 <code v-pre="">checkbox</code> 一般用于状态标记，需要和提交操作配合。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-switch v-model=\&quot;checked\&quot;&gt;&lt;/v-switch&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      checked: true\n    })\n  };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-switch v-model="checked"></v-switch>
</template>


</div>
                  <div slot="description"><h4 id="jian-dan"><a class="header-anchor" href="#jian-dan" aria-hidden="true">¶</a> 简单</h4>
<p>最简单的用法。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
    })
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 简单
  最简单的用法。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
    })
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-switch :disabled=\&quot;disabled\&quot;&gt;&lt;/v-switch&gt;\n  &lt;br&gt;&lt;br&gt;\n  &lt;v-button type=\&quot;primary\&quot; @click=\&quot;_toogle\&quot;&gt;Toggle disabled&lt;/v-button&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      disabled: false\n    }),\n    methods: {\n      _toogle() {\n        this.disabled = !this.disabled\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-switch :disabled="disabled"></v-switch>
  <br><br>
  <v-button type="primary" @click="_toogle">Toggle disabled</v-button>
</template>


</div>
                  <div slot="description"><h4 id="bu-ke-yong"><a class="header-anchor" href="#bu-ke-yong" aria-hidden="true">¶</a> 不可用</h4>
<p>Switch 失效状态。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"_toogle"</span>&gt;</span>Toggle disabled<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
    }),
    <span class="hljs-attr">methods</span>: {
      _toogle() {
        <span class="hljs-keyword">this</span>.disabled = !<span class="hljs-keyword">this</span>.disabled
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 不可用
  Switch 失效状态。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"_toogle"</span>&gt;</span>Toggle disabled<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
    }),
    <span class="hljs-attr">methods</span>: {
      _toogle() {
        <span class="hljs-keyword">this</span>.disabled = !<span class="hljs-keyword">this</span>.disabled
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-switch&gt; \n  &lt;span slot=\&quot;checkedChildren\&quot;&gt;开&lt;/span&gt;\n  &lt;span slot=\&quot;unCheckedChildren\&quot;&gt;关&lt;/span&gt;\n&lt;/v-switch&gt;\n&lt;br&gt;&lt;br&gt;\n&lt;v-switch&gt; \n  &lt;span slot=\&quot;checkedChildren\&quot;&gt;\n    &lt;i class=\&quot;anticon anticon-check\&quot;&gt;&lt;/i&gt;\n  &lt;/span&gt;\n  &lt;span slot=\&quot;unCheckedChildren\&quot;&gt;\n    &lt;i class=\&quot;anticon anticon-cross\&quot;&gt;&lt;/i&gt;\n  &lt;/span&gt;\n&lt;/v-switch&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-switch> 
  <span slot="checkedChildren">开</span>
  <span slot="unCheckedChildren">关</span>
</v-switch>
<br><br>
<v-switch> 
  <span slot="checkedChildren">
    <i class="anticon anticon-check"></i>
  </span>
  <span slot="unCheckedChildren">
    <i class="anticon anticon-cross"></i>
  </span>
</v-switch>
</div>
                  <div slot="description"><h4 id="wen-zi-he-tu-biao"><a class="header-anchor" href="#wen-zi-he-tu-biao" aria-hidden="true">¶</a> 文字和图标</h4>
<p>带有文字和图标。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span> 
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"checkedChildren"</span>&gt;</span>开<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"unCheckedChildren"</span>&gt;</span>关<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span> 
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"checkedChildren"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"anticon anticon-check"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"unCheckedChildren"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"anticon anticon-cross"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 文字和图标
  带有文字和图标。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span> 
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"checkedChildren"</span>&gt;</span>开<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"unCheckedChildren"</span>&gt;</span>关<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span> 
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"checkedChildren"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"anticon anticon-check"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"unCheckedChildren"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"anticon anticon-cross"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-switch&gt;&lt;/v-switch&gt;\n&lt;br&gt;\n&lt;v-switch size=\&quot;small\&quot;&gt;&lt;/v-switch&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-switch></v-switch>
<br>
<v-switch size="small"></v-switch>
</div>
                  <div slot="description"><h4 id="liang-chong-da-xiao"><a class="header-anchor" href="#liang-chong-da-xiao" aria-hidden="true">¶</a> 两种大小</h4>
<p><code>size='small'</code> 表示小号开关</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 两种大小
  `size='small'` 表示小号开关
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-switch v-model=\&quot;checkStatus\&quot; :true-value=\&quot;1\&quot; :false-value=\&quot;0\&quot;&gt;&lt;/v-switch&gt;\n  &lt;v-button @click=\&quot;getStatus\&quot; size=\&quot;small\&quot; type=\&quot;primary\&quot;&gt;获取状态码&lt;/v-button&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      checkStatus: 1\n    }),\n    methods: {\n      getStatus(){\n        this.$message.info(\&quot;当前状态码为：\&quot; + this.checkStatus);\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-switch v-model="checkStatus" :true-value="1" :false-value="0"></v-switch>
  <v-button @click="getStatus" size="small" type="primary">获取状态码</v-button>
</template>


</div>
                  <div slot="description"><h4 id="zi-ding-yi-zhuang-tai-zhi"><a class="header-anchor" href="#zi-ding-yi-zhuang-tai-zhi" aria-hidden="true">¶</a> 自定义状态值</h4>
<p>属性名称：<code>true-value</code>  <code>false-value</code><br>
以下例子定义：选中时为 1 ，未选中时为 0<br>
组件当前状态为选中</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkStatus"</span> <span class="hljs-attr">:true-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:false-value</span>=<span class="hljs-string">"0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"getStatus"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>获取状态码<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">checkStatus</span>: <span class="hljs-number">1</span>
    }),
    <span class="hljs-attr">methods</span>: {
      getStatus(){
        <span class="hljs-keyword">this</span>.$message.info(<span class="hljs-string">"当前状态码为："</span> + <span class="hljs-keyword">this</span>.checkStatus);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 自定义状态值
  属性名称：`true-value`  `false-value`
  以下例子定义：选中时为 1 ，未选中时为 0
  组件当前状态为选中
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkStatus"</span> <span class="hljs-attr">:true-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:false-value</span>=<span class="hljs-string">"0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"getStatus"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>获取状态码<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">checkStatus</span>: <span class="hljs-number">1</span>
    }),
    <span class="hljs-attr">methods</span>: {
      getStatus(){
        <span class="hljs-keyword">this</span>.$message.info(<span class="hljs-string">"当前状态码为："</span> + <span class="hljs-keyword">this</span>.checkStatus);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="switch-props"><a class="header-anchor" href="#switch-props" aria-hidden="true">¶</a> Switch Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定当前是否选中</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabeled</td>
<td>指定当前是否被禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>开关大小（&quot;default&quot; or &quot;small&quot;）</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>slot:checkedChildren</td>
<td>选中时的内容</td>
<td>slot node</td>
<td>-</td>
</tr>
<tr>
<td>slot:unCheckedChildren</td>
<td>非选中时的内容</td>
<td>slot node</td>
<td>-</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时自定义值</td>
<td>Any</td>
<td>-</td>
</tr>
<tr>
<td>false-value</td>
<td>未选中时自定义值</td>
<td>Any</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
  export default {
    data: ()=> ({
      checked:true,
      checkStatus:1,
      disabled: false
    }),
    methods: {
      _toogle() {
        this.disabled = !this.disabled
      },
      getStatus(){
        this.$message.info("当前状态码为：" + this.checkStatus);
      }
    }
  }
</script>


